@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  box-sizing: border-box;
  color: black;
}

.App {
  float: none;
}

header {
  width: 100%;
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
  align-items: center;
  margin-bottom: 2em;
}

header .image-container {
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
  align-items: center;
}

.logo {
  width: 10rem;
}

.team-icon {
  position: fixed;
  top: 5%;
  right: 10em;
  width: 4%;
}

.team-icon img {
  width: 100%;
}

@media screen and (max-width: 800px) {
  .logo {
    width: 20%;
  }
  .title {
    width: 60%;
  }

  .team-icon {
    width: 10%;
    left: 3%;
  }
}

@media screen and (max-width: 1200px) {
  .team-icon {
    width: 10%;
    left: 3%;
  }
}

.lds-roller {
  display: block;
  width: 80px;
  height: 80px;
}
.lds-roller div {
  -webkit-animation: lds-roller 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
          animation: lds-roller 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
  transform-origin: 40px 40px;
}
.lds-roller div:after {
  content: " ";
  display: block;
  position: absolute;
  width: 7px;
  height: 7px;
  border-radius: 50%;
  background: black;
  margin: -4px 0 0 -4px;
}
.lds-roller div:nth-child(1) {
  -webkit-animation-delay: -0.036s;
          animation-delay: -0.036s;
}
.lds-roller div:nth-child(1):after {
  top: 63px;
  left: 63px;
}
.lds-roller div:nth-child(2) {
  -webkit-animation-delay: -0.072s;
          animation-delay: -0.072s;
}
.lds-roller div:nth-child(2):after {
  top: 68px;
  left: 56px;
}
.lds-roller div:nth-child(3) {
  -webkit-animation-delay: -0.108s;
          animation-delay: -0.108s;
}
.lds-roller div:nth-child(3):after {
  top: 71px;
  left: 48px;
}
.lds-roller div:nth-child(4) {
  -webkit-animation-delay: -0.144s;
          animation-delay: -0.144s;
}
.lds-roller div:nth-child(4):after {
  top: 72px;
  left: 40px;
}
.lds-roller div:nth-child(5) {
  -webkit-animation-delay: -0.18s;
          animation-delay: -0.18s;
}
.lds-roller div:nth-child(5):after {
  top: 71px;
  left: 32px;
}
.lds-roller div:nth-child(6) {
  -webkit-animation-delay: -0.216s;
          animation-delay: -0.216s;
}
.lds-roller div:nth-child(6):after {
  top: 68px;
  left: 24px;
}
.lds-roller div:nth-child(7) {
  -webkit-animation-delay: -0.252s;
          animation-delay: -0.252s;
}
.lds-roller div:nth-child(7):after {
  top: 63px;
  left: 17px;
}
.lds-roller div:nth-child(8) {
  -webkit-animation-delay: -0.288s;
          animation-delay: -0.288s;
}
.lds-roller div:nth-child(8):after {
  top: 56px;
  left: 12px;
}
@-webkit-keyframes lds-roller {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
@keyframes lds-roller {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

* {
  box-sizing: border-box;
  color: black;
}

.cards-wrapper {
  display: flex;
  flex-flow: row wrap;
  width: 70%;
  margin: 0 15%;
  justify-content: center;
}

.card {
  text-decoration: none;
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;
  border: solid black 1px;
  border-radius: 3%;
  width: 31%;
  height: 35vw;
  margin: 1%;
  box-shadow: 0 0 0.5em grey;
}

.card h2 {
  margin: 0;
}

.card p {
  margin-top: 0;
}

.card .image-container {
  display: flex;
  justify-content: center;
  width: 80%;
  height: 20vw;
  margin: 0 10%;
}

.card .pokemon-image {
  height: 100%;
  width: 80%;
  transition: transform;
  transition-duration: 0.5s;
}

.card:hover .pokemon-image {
  transform: scale(1.2);
}

.card:hover {
  background-color: #ffe031;
  border: solid #396bba 5px;
  box-shadow: 0 0 1.5em 0.5em #396bba;
}

.card:hover .team-button {
  background-color: #ffe031;
}

.card .card-header {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  width: 90%;
  height: 10%;
  margin: 1em;
}

.team-button {
  background-color: transparent;
  border: none;
  outline: transparent;
  position: absolute;
  right: 0;
  height: 100%;
  cursor: pointer;
}

.team-button img {
  height: 60%;
}

.card .types {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 1em;
  width: 80%;
}

.card .types img {
  width: 25%;
}

@media screen and (max-width: 1400px) {
  .card {
    height: 45vw;
  }
}

@media screen and (max-width: 1200px) {
  .cards-wrapper {
    flex-flow: row wrap;
    align-items: center;
    width: 100%;
    margin: 0;
  }
  .card {
    width: 40%;
    height: 60vw;
  }

  .card .image-container {
    height: 30vw;
  }

  .lds-roller {
    margin: 0 5em;
  }
}

@media screen and (max-width: 800px) {
  .cards-wrapper {
    flex-flow: column nowrap;
    align-items: center;
    width: 100%;
    margin: 0;
  }
  .card {
    width: 70%;
    height: 100vw;
  }

  .lds-roller {
    margin: 0;
  }

  .card .image-container {
    height: 40vw;
  }

  .card h2 {
    font-size: 2.5em;
  }
}

@media screen and (max-width: 400px) {
  .card h2 {
    font-size: 1.5em;
  }
  .card {
    height: 110vw;
  }
}

* {
  box-sizing: border-box;
}

.container {
  border: solid black 3px;
  border-radius: 1em;
  display: flex;
  flex-flow: column nowrap;
  box-shadow: 0 0 0.75em rgb(180, 171, 171);
}

.details-wrapper {
  width: 80%;
  margin: 1em auto;
  display: grid;
  grid-gap: 1em;
  gap: 1em;
  grid-template-columns: 1fr 2fr;
  grid-template-rows: 1fr 2fr 1fr 1fr 1fr;
  grid-template-areas:
    "presentation caracteristics"
    "presentation stats"
    "types stats"
    "evolution evolution"
    "evolution evolution";
}

.container-title {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  border-bottom: solid black 1px;
}

h2 {
  font-family: "Bebas Neue", cursive;
  font-size: 2.5em;
}

h3 {
  font-family: "Bebas Neue", cursive;
  font-size: 2em;
}

p {
  text-align: center;
  font-size: 1.5em;
}

span {
  text-decoration: underline;
}

a {
  text-decoration: none;
}

.container-content {
  display: flex;
  flex-flow: column nowrap;
}

.pokemon-presentation {
  grid-area: presentation;
  justify-content: center;
  align-items: center;
}

.pokemon-presentation .container-title {
  height: 4em;
}

.pokemon-presentation .container-title h2 {
  margin: 0.5em 0;
}

.pokemon-presentation .container-content {
  justify-content: space-around;
  align-items: center;
  flex-grow: 1;
}

.pokemon-presentation .image-container {
  height: 35vh;
}

.pokemon-presentation .pokemon-image {
  height: 100%;
}

.pokemon-presentation .pokemon-description {
  margin: 1em;
}

.pokemon-types {
  grid-area: types;
  justify-content: center;
  align-items: center;
}

.pokemon-types .container-title {
  height: 4em;
}

.pokemon-types .container-content {
  flex-grow: 1;
  justify-content: center;
  align-items: center;
  width: 80%;
}

.pokemon-types .image-container {
  width: 100%;
  text-align: center;
}

.pokemon-types img {
  width: 25%;
}

.pokemon-caracteristics {
  justify-content: center;
  align-items: center;
}

.pokemon-caracteristics .container-title {
  height: 4em;
}

.pokemon-caracteristics .container-content {
  grid-area: caracteristics;
  flex-flow: row nowrap;
  justify-content: space-around;
  align-items: center;
  width: 100%;
  flex-grow: 1;
}

.pokemon-stats {
  grid-area: stats;
  justify-content: center;
  align-items: center;
}

.pokemon-stats .container-title {
  height: 4em;
}

.pokemon-stats .container-content {
  justify-content: center;
  align-items: center;
  flex-grow: 1;
  width: 100%;
}

.pokemon-stats .progress-wrapper {
  width: 100%;
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;
}

.pokemon-presentation .image-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 60%;
}

.pokemon-presentation .pokemon-image {
  width: 80%;
}

.pokemon-presentation .pokemon-description {
  text-align: center;
}

.pokemon-stats .stat-name {
  text-align: center;
  margin-top: 0.5em;
  margin-bottom: 0;
}

progress {
  width: 45em;
  height: 3em;
  margin: 1em;
}

.evolution-container {
  grid-area: evolution;
  border-radius: 1em;
}

.evolution-container .container-title {
  height: 4em;
}

.evolution-container .container-content {
  flex-flow: row nowrap;
  justify-content: space-evenly;
  align-items: center;
  flex-grow: 1;
}

.evolution-container .image-container {
  display: flex;
  justify-content: center;
  align-items: center;
  transition: transform 0.5s;
  height: 30vh;
  margin: 2em;
}

.evolution-container .pokemon-image {
  height: 100%;
}

.evolution-container .image-container:hover {
  transform: scale(1.2);
}

@media screen and (max-width: 1200px) {
  .details-wrapper {
    display: flex;
    flex-flow: column nowrap;
  }
  progress {
    width: 90%;
    height: 3em;
    margin: 1em;
  }
  .evolution-container .container-content {
    flex-flow: column nowrap;
  }

  .evolution-container .image-container {
    height: 40vw;
  }

  .pokemon-types .image-container {
    margin: 1em 0;
  }
}

